    .cards {
        display: flex;
        justify-content: space-around;
        flex-direction: row;
        
    }

    .bg-color {
        background: linear-gradient(74deg,red 0,green 9%,blue 20%,yellow 24%,gray 35%,black 44%,pink 50%, 56%,blue 75%,red 100%);
        background-color: transparent;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-size: 400% 100%;
        font-weight: bolder;
        font-size: 3rem;
        line-height: 2;
    }

    .heading{
        color: lightgray;
        background-color: transparent;
        -webkit-background-clip: text;
        /* -webkit-text-fill-color: transparent; */
        background-size: 400% 100%;
        /* margin-top: 17px; */
        font-weight: bolder;
        font-size: 3rem;
    }


    .hide-scrollbar {
        -ms-overflow-style: none;  /* Internet Explorer 10+ */
        scrollbar-width: none;  /* Firefox */
      }
      
      .hide-scrollbar::-webkit-scrollbar {
        display: none;  /* Safari and Chrome */
      }
      
      .container {
        max-width: 80%;
        margin: 0 auto;
      }